.page-header {
  width: 100vw;
  z-index: 2;
  color: #fff;
  background-color: #3d6970;
  font-family: Ubuntu, sans-serif;
  font-size: 20px;
  font-weight: 700;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 0 10px rgba(0, 0, 0, .6);
}

.header {
  display: none;
}

@media screen and (min-width: 768px) {
  .header {
    justify-content: space-between;
    align-items: center;
    display: flex;
  }
}

.logo {
  align-items: center;
  transition: all .5s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.logo:hover {
  transform: scale(1.2);
}

.logo__photo {
  margin-right: 10px;
}

.navigation__pages {
  align-items: center;
  padding: 0;
  display: flex;
}

.navigation__link {
  color: #fff;
  text-decoration: none;
}

.navigation__item {
  color: #fff;
  width: 120px;
  height: 40px;
  letter-spacing: .1em;
  text-align: center;
  z-index: 2;
  border-radius: 4px;
  font-size: 20px;
  line-height: 2em;
  transition: all .5s;
  position: relative;
  overflow: hidden;
}

.navigation__item:not(:last-child) {
  margin-right: 10px;
}

.navigation__item:hover .navigation__link {
  color: #3d6970;
}

.navigation__item span {
  width: 25%;
  height: 100%;
  right: calc((var(--n)  - 1) * 25%);
  transition: all .5s;
  transition-delay: calc((var(--n)  - 1) * .1s);
  z-index: -1;
  background-color: #fff;
  border-radius: 50%;
  position: absolute;
  transform: translateY(150%);
}

.navigation__item:hover span {
  transform: translateY(0)scale(2);
}

.navigation__item span:nth-child(1) {
  --n: 1;
}

.navigation__item span:nth-child(2) {
  --n: 2;
}

.navigation__item span:nth-child(3) {
  --n: 3;
}

.navigation__item span:nth-child(4) {
  --n: 4;
}

.navigation__item--current .navigation__link {
  color: #3d6970;
}

.navigation__item--current span {
  transform: translateY(0)scale(2);
}

.dropdown {
  display: inline-block;
  position: relative;
}

.dropdown-content {
  min-width: 300px;
  z-index: 2;
  opacity: .95;
  background-color: #fff;
  border-radius: 4px;
  padding: 10px;
  display: none;
  position: absolute;
  top: 40px;
  right: 10px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, .2);
}

@keyframes open {
  0% {
    opacity: 0;
    display: block;
  }

  100% {
    opacity: 1;
    display: block;
  }
}

.header-contact__item:hover {
  background-color: #ddd;
}

.dropdown:hover .dropdown-content {
  animation-name: open;
  animation-duration: 2s;
  display: block;
}

.dropdown:hover span {
  transform: translateY(0)scale(2);
}

.dropdown:hover .navigation__link {
  color: #3d6970;
}

.header-contact__icon-telephone {
