.open-menu.is-open {
  min-height: 40px;
  min-width: 40px;
  fill: #3d6970;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  transition: background-color .5s cubic-bezier(.4, 0, .2, 1), fill .5s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.open-menu:hover, .open-menu:focus {
  fill: #fff;
  background-color: rgba(245, 242, 242, .1);
}

.close-menu {
  display: none;
}

.close-menu.is-open {
  min-height: 40px;
  min-width: 40px;
  fill: #3d6970;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  transition: background-color .5s cubic-bezier(.4, 0, .2, 1), fill .5s cubic-bezier(.4, 0, .2, 1);
